.main {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;

  &__card {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    align-content: center;
    max-width: containerWidth($card-width, $card-margin-right);
    margin: 0 auto;
    padding: 50px 0;
  }
}
